export default class StringBuilder {
    constructor(s = '') {
        this.s = s;
        this.strs = new Array();
        this.dirty = false;
        if (s)
            this.strs.push(s);
    }
    append(s) {
        if (s) {
            this.strs.push(s);
            this.dirty = true;
        }
        return this;
    }
    toString() {
        if (this.dirty) {
            this.s = this.strs.join('');
            this.dirty = false;
        }
        return this.s;
    }
}
