export default class Bookmark {
    constructor(line = 0, col = 0, pos = 0) {
        this.line = line;
        this.col = col;
        this.pos = pos;
    }
    append(c) {
        this.pos++;
        this.col++;
        if (c === '\n') {
            this.line++;
            this.col = 0;
        }
        return c;
    }
    clone() {
        return new Bookmark(this.line, this.col, this.pos);
    }
    toString() {
        return `Bookmark(line=${this.line}, col=${this.col}, pos=${this.pos})`;
    }
}
